import { AfterViewChecked, ChangeDetectorRef, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { IFileFormat, IOptionsPsActionImage } from '../../../commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from '../../../commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { IOmniDocumentsReportObject } from '../../psServices/models/ps-common-bussiness-interfaces';
import { OmniCommonService } from '../../psServices/omni-common/omni-common.service';
import { IOptionsPsDocumentsListComponent } from './ps-documents-list.component.interface';

@Component({
  selector: 'ps-documents-list',
  templateUrl: './ps-documents-list.component.html',
  styleUrls: ['./ps-documents-list.component.scss'],
})
export class PsDocumentsListComponent extends PsTemplateListPage implements OnInit, AfterViewChecked, OnDestroy {
  @Input() options: IOptionsPsDocumentsListComponent;
  noDataMessage = "no_data_found_key";
  noFilteredReportsMessage = "no_filtered_details_found_key";
  public pdfOptions: IOptionsPsActionImage = {
    imageName: 'pdf.png',
    imagePath: 'assets/branding/default_theme/imgs/'
  }
  public excelOptions: IOptionsPsActionImage = {
    imageName: 'excel.png',
    imagePath: 'assets/branding/default_theme/imgs/'
  }
  constructor(private omniCommonService: OmniCommonService, private cdRef?: ChangeDetectorRef) {
    super();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    super.ngOnDestroy();
  }

  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }

  export(option: number, item: IOmniDocumentsReportObject) {
    let reportParametersList = this.options.itemCard;
    reportParametersList["repFltfilterFromDate"] = this.options.fromDate;
    reportParametersList["repFltfilterToDate"] = this.options.toDate;
    if (option == 1) {
      this.omniCommonService.exportData(IFileFormat.PDF, reportParametersList, item.operId);
    } else {
      this.omniCommonService.exportData(IFileFormat.EXCEL, reportParametersList, item.operId);
    }
  }
}
